export function authenticationFailed(ctx) {
    ctx.status = 401;
    ctx.body = {
        message: 'Email or password error'
    };
}

export function emailIsNotExisted(ctx) {
    ctx.status = 422;
    ctx.body = {
        message: 'User is not existed',
        errors: [
            {
                'resource': 'User',
                'field': 'email',
                'code': 'missing_field'
            }
        ]
    };
}
